#partnersContainer {
  background-color: rgb(43, 43, 43);
  width: 100%;
  overflow: hidden;
}

#partners {
  display: grid;
  grid-template-columns: repeat(3, minmax(200px, 1fr));
  grid-template-rows: repeat(1, minmax(420px, 1fr));
  transition: transform 0.5s ease-in-out;
}

.slider-image {
  width: 80%;
}

#partnersHeader {
  font-size: 24px;
  padding-left: 11%;
  padding-top: 2%;
  color: rgb(244, 244, 244);
  margin: 0;
}
